
.App {
  width: 1100px;
  margin: 10px auto;

}
.App-header{
  display: flex;
  flex-direction: column;
  justify-content: center;

  align-items: center;
  color:#1864c4;
}

.form {
  width: 100%;
  height: 100%;
  padding: 30px;
}
.repairs-form {
 

  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
  padding: 20px;
}
.repairs-image {
  width: 500px;
}

.repairs-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.design-form {

  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
.price{
  font-weight: 900;
}
.politics-button{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;

}
.footer{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 20px;
  background-color: #1864c4;
  border-radius: 20px;
  color: white;
}

.contact{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
a{
  text-decoration: none;
  color: white;
}
.payment{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.payment-image{
  width: 50px;
  height: 50px;
}


@media (max-width:1100px) {
  .App{
    width: fit-content;
  }

  .repairs-form {
    flex-direction: column;
    align-items: center;
  }
  .repairs-image {
    width: 400px;
  }
  .design-form {
    flex-direction: column;
    align-items: center;
  }
  
}

@media (max-width:510px){
  .repairs-image{
    width: 250px;
  }
}